.container {
  min-width: 320px;
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
  position: relative;
}

.fav-ingr-is-hidden {
  visibility: hidden;
  pointer-events: none;
  position: absolute;
}

.favorite-title {
  margin-top: 87px;
  margin-bottom: 40px;
  font-size: 24px;
  font-weight: 500;
  line-height: 1.16;
}

.ingredients-gallery {
  width: 100%;
  flex-wrap: wrap;
  row-gap: 20px;
  display: flex;
}

.ingredient-card {
  width: 335px;
  background: var(--bg-card-color-theme-light);
  border-radius: 20px;
  padding: 20px;
  box-shadow: 0 4px 46px rgba(0, 0, 0, .05);
}

.ingredient-card:last-child {
  margin-bottom: 80px;
}

.ingredient-header {
  margin-bottom: 4px;
  font-size: 20px;
  font-weight: 600;
  line-height: 1.33;
}

.alcoholic-ing {
  color: var(--modal-first-text-color-theme-light);
  margin-bottom: 14px;
  line-height: 1.28;
}

.ingredient-description {
  width: 295px;
  height: 54px;
  text-overflow: ellipsis;
  white-space: wrap;
  margin-bottom: 20px;
  font-size: 14px;
  line-height: 1.28;
  overflow: hidden;
}

.ingredient-btns {
  gap: 15px;
  display: flex;
}

.ingredient-learn-more-btn {
  width: 232px;
  height: 48px;
  border: 1px solid var(--primary-accent-color);
  border-radius: 12px;
  font-weight: 600;
}

.learn-more-btn {
  border: 1px solid var(--primary-accent-color);
  color: var(--primary-text-color-theme-light);
  letter-spacing: .28px;
  text-transform: uppercase;
  background: none;
  border-radius: 12px;
  font-family: Poppins;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 20px;
}

.learn-more-btn:hover, .learn-more-btn:focus, .learn-more-btn:active {
  background: var(--primary-accent-color);
  color: var(--bg-card-color-theme-light);
}

.delete-btn {
  width: 56px;
  height: 56px;
  background: var(--primary-accent-color);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 12px;
  outline: none;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  display: flex;
}

.delete-btn:hover, .delete-btn:focus, .delete-btn:active {
  background: var(--secondary-accent-color);
  color: var(--bg-card-color-theme-light);
}

.delete-icon {
  stroke: var(--bg-card-color-theme-light);
  height: 18px;
  width: 18px;
}

.not-found-container {
  width: 220px;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  margin: 100px auto 140px;
  display: flex;
}

.not-found-container.is-hidden {
  display: none;
}

.not-found-txt-span {
  color: var(--secondary-accent-color);
  font-style: italic;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }

  .ingredients-gallery {
    gap: 32px 16px;
  }

  .favorite-title {
    margin-top: 105px;
    font-size: 40px;
    line-height: 1.2;
  }

  .ingredient-card {
    width: 344px;
    height: 242px;
  }

  .ingredient-card:last-child {
    margin-bottom: 100px;
  }

  .ingredient-header {
    font-size: 20px;
  }

  .ingredient-learn-more-btn {
    width: 225px;
    height: 56px;
  }

  .not-found-container {
    width: 372px;
    gap: 40px;
    margin-top: 60px;
    margin-bottom: 140px;
  }

  .not-found-img {
    width: 142px;
    height: 122px;
  }

  .not-found-txt {
    width: 372px;
    font-size: 24px;
    line-height: 1.17;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    max-width: 1280px;
    padding: 0 100px;
  }

  .ingredients-gallery {
    gap: 44px 24px;
  }
}

body.dark-theme {
  background: #121212;
}

body.dark-theme .ingredient-card {
  background-color: var(--bg-card-modal-color-theme-dark);
}

.body.dark-theme .learn-more-btn {
  color: var(--color-background-white);
}

.body.dark-theme .ingredient-description, body.dark-theme .alcoholic-ing {
  color: var(--modal-second-text-color-theme-dark);
}

